export const typeTags = {
	howTo: 'how-to',
	overview: 'overview',
	quickstart: 'quickstart',
	tutorial: 'tutorial',
	concept: 'concept-article'
};

export function extractArticleInfo(article: {
	itemId: string;
	category: string;
	score: number;
	metadata: {
		id: string;
		tenant: string;
		content: string;
		lastUpdated: string;
		pageType: string;
		url: string;
		title: string;
		depotName: string;
		msService: string;
		msTopic: string;
		msSubservice?: string;
		msProd?: string;
	};
}): { score: number; content: string; url: string } {
	const { score, metadata } = article;
	const { content, url } = metadata;
	return { score, content, url };
}

export function getFirstNWords(content: string, n: number): string {
	return content.split(' ').slice(0, n).join(' ');
}
