/* eslint-disable @typescript-eslint/no-var-requires */
'use strict';
import axios from 'axios';
import { workspace } from 'vscode';
import { postError, postErrorWithButton } from '../helper/common';

const config = workspace.getConfiguration('docs.templates');
export const docsToken: string = String(config.get('docs_token'))
	.trim()
	.replace(/^(Bearer )?/i, '');
export const aoaiToken: string = String(config.get('ai_token')).trim();

const MAX_TOKENS = 12000; // API limit

export const docsEndpoint =
	'https://learn.microsoft.com/api/knowledge/playground/document/relevantItems';
export const aoaiEndpoint =
	'https://openai-jasdeb.openai.azure.com/openai/deployments/chat35/chat/completions?api-version=2023-03-15-preview';

export async function makeAOAIRequest(prompt: string, minResponseTokens: number = 1000) {
	// min_response_tokens is the minimum number of tokens we need to leave for the response
	// So, if expecting short responses, set this to a low number to allow more tokens for the prompt, and vice versa
	try {
		// start with a generous estimate for token length
		let promptTokens: number = Math.ceil(prompt.length / 3);
		while (promptTokens + minResponseTokens > MAX_TOKENS) {
			prompt = prompt.slice(0, -1);
			promptTokens = Math.ceil(prompt.length / 3);
		}
		const response = await axios.post(
			aoaiEndpoint,
			{
				messages: [
					{
						role: 'system',
						content: 'You are an AI assistant that helps in technical writing.'
					},
					{
						role: 'user',
						content: prompt
					}
				],
				temperature: 0.3,
				top_p: 1,
				frequency_penalty: 0,
				presence_penalty: 0,
				max_tokens: MAX_TOKENS - promptTokens,
				stop: null
			},
			{
				headers: {
					'Api-key': `${aoaiToken}`
				}
			}
		);
		const res = response.data;
		return res.choices[0].message.content;
	} catch (err) {
		if (err.response && err.response.status === 400) {
			postError('Error: Try shortening your input.');
		} else if (err.response && err.response.status === 401) {
			postErrorWithButton(
				'Error: Invalid Azure OpenAI token. Please go to Settings > Docs > Templates: Ai_token and enter a valid token.',
				'Go to Settings',
				'workbench.action.openSettings2'
			);
		} else if (err.response && err.response.status === 429) {
			postErrorWithButton(
				'Error: Too many requests are being sent from this Azure OpenAI token.',
				'Go to Settings',
				'workbench.action.openSettings2'
			);
		} else if (err.response) {
			postError('Error: There was an issue with the service. Please try again later.');
		}
		throw err;
	}
}

export async function makeKnowledgeIndexRequest(description: string) {
	try {
		const response = await axios.post(
			docsEndpoint,
			{ input: description },
			{
				headers: {
					Authorization: `Bearer ${docsToken}`
				}
			}
		);
		return response.data;
	} catch (err) {
		if (err.response && err.response.status >= 400 && err.response.status < 500) {
			postErrorWithButton(
				'Error: Invalid Microsoft Docs token. Please go to Settings > Docs > Templates: Docs_token and enter a valid token.',
				'Go to Settings',
				'workbench.action.openSettings2'
			);
		} else if (err.response && err.response.status >= 500) {
			postError('Error: There was an issue with the service. Please try again later.');
		}
		throw err;
	}
}
