/* eslint-disable @typescript-eslint/no-var-requires */
'use strict';

import { Uri, ViewColumn, env, window, ProgressLocation } from 'vscode';
import { postError, showStatusMessage } from '../helper/common';
import { getSummaryPrompt, getOutlinePrompt, getMarkdownPrompt } from '../ai-feature/prompts';
import { applyAITemplate } from './quick-pick-controller';
import { makeAOAIRequest, makeKnowledgeIndexRequest } from '../ai-feature/api-settings';
import { getWebviewContent } from '../ai-feature/webview';
import { extractArticleInfo } from '../ai-feature/helper';
import { availableTemplates } from '../ai-feature/templates/available-templates';

// Threshold for similarity between article description and related articles to be considered duplicates
const SIMILARITY_THRESHOLD = 0.89;
// Threshold for the length of the article context to determine if the user provided enough context information, or if the knowledge index should be called
const CONTENT_THRESHOLD = 4000;

// Open the WebView panel and return a promise that will be resolved when the user submits the article description
export async function promptArticleDescription(): Promise<string | undefined> {
	const panel = window.createWebviewPanel(
		'articleDescription',
		'Article Description',
		ViewColumn.Active,
		{
			enableScripts: true
		}
	);
	panel.webview.html = getWebviewContent();

	// Create a promise that will be resolved when the user submits the article description or rejects if there is an error
	return new Promise<string | undefined>((resolve, reject) => {
		let loadingRemoved = false;
		const removeLoading = () => {
			if (!loadingRemoved) {
				loadingRemoved = true;
				panel.webview.postMessage({ command: 'removeLoading' });
			}
		};
		panel.webview.onDidReceiveMessage(async message => {
			if (message.command === 'submit') {
				const articleDescription = message.text;
				panel.webview.postMessage({ command: 'setLoading' });
				try {
					await generateTemplate(articleDescription, message.context);
					resolve(articleDescription || undefined);
					removeLoading();
				} catch (err) {
					showStatusMessage(err);
					reject(err);
					removeLoading();
				} finally {
					removeLoading();
				}
			}
		});
	});
}

// Make the necessary API calls to get information from the knowledge index and use AI to generate a template
async function generateTemplate(description: string, notes: string) {
	try {
		const { userContext, knowledgeIndexContext, duplicate, articleLinks } = await setContext(
			description,
			notes
		);
		if (duplicate) {
			showDuplicateArticle(duplicate);
		}

		await window.withProgress(
			{
				location: ProgressLocation.Notification,
				title:
					'Generating your template (This will take a minute, feel free to work in other windows.)',
				cancellable: false
			},
			async progress => {
				progress.report({ increment: 25, message: 'Analyzing description...' });
				const contextSummary = await makeAOAIRequest(
					getSummaryPrompt(description, knowledgeIndexContext)
				);
				// The API will return something like "false" or "in_scope=false" if the description is inappropriate or irrelevant
				if (contextSummary.toUpperCase().replace(/\s+/g, '').includes('SCOPE=FALSE')) {
					postError(
						`That doesn't seem like a description for an article, or your description may not have enough content to generate a template from. Please try again.`
					);
					return Promise.reject(new Error('Invalid article description'));
				}
				progress.report({ increment: 25, message: 'Building outline...' });

				const type = getType(contextSummary);
				const outline = await makeAOAIRequest(
					getOutlinePrompt(description, String(contextSummary) + userContext, type, articleLinks)
				);
				progress.report({ increment: 25, message: 'Finalizing template...' });

				const template = await makeAOAIRequest(getMarkdownPrompt(outline, type), 4000);
				progress.report({ increment: 24 });
				applyAITemplate(template);
			}
		);
	} catch (err) {
		showStatusMessage(err);
		return Promise.reject(err);
	}
}

// Return the string that matches the type of template to generate, ensure it matches one of the available templates
function getType(input: string) {
	input = input.toLowerCase();
	const allowedTypes = Object.keys(availableTemplates);
	const match = input.match(/type=(\S+)/);
	if (match) {
		const type = match[1];
		for (const allowedType of allowedTypes) {
			if (type.includes(allowedType)) {
				return allowedType;
			}
		}
	} else {
		for (const allowedType of allowedTypes) {
			if (input.includes(allowedType)) {
				return allowedType;
			}
		}
	}
	// default to concept-article if no type is found, since it is the most general template
	return 'concept-article';
}

async function setContext(description: string, userContext: string) {
	const inputLength = description.length + userContext.length;
	let knowledgeIndexContext = '';
	let articleLinks: string[] = [];
	let duplicate = '';

	// If the user provided a great amount of context information, don't call the knowledge index
	if (inputLength > CONTENT_THRESHOLD) {
		duplicate = await getDuplicateArticle(description);
	} else {
		// Otherwise, call the knowledge index to get content from related articles as context information
		const relatedContent = await getRelatedArticlesAndDuplicate(description);
		knowledgeIndexContext = relatedContent.articleContent;
		articleLinks = relatedContent.articleLinks;
		duplicate = relatedContent.duplicate;
	}
	return { userContext, knowledgeIndexContext, duplicate, articleLinks };
}

function showDuplicateArticle(duplicate: string) {
	const infoMessage = 'It looks like there may already be an article on this topic.';
	window.showInformationMessage(infoMessage, 'Visit article').then(selection => {
		if (selection === 'Visit article') {
			env.openExternal(Uri.parse(duplicate));
		}
	});
}

async function getRelatedArticlesAndDuplicate(description: string) {
	let res: any = '';
	let articleContent = '';
	let duplicate: string;
	const articleLinks: string[] = [];

	res = await makeKnowledgeIndexRequest(description);
	res.forEach((article: any) => {
		const { score, content, url } = extractArticleInfo(article);
		articleContent += content;
		articleLinks.push(url);
		if (score >= SIMILARITY_THRESHOLD && !duplicate && Uri.parse(url).scheme === 'https') {
			duplicate = url;
		}
	});

	return { articleContent, articleLinks, duplicate };
}

async function getDuplicateArticle(description: string) {
	let res: any = '';
	let duplicate: string;

	res = await makeKnowledgeIndexRequest(description);
	res.forEach((article: any) => {
		const { score, content, url } = extractArticleInfo(article);
		if (score >= SIMILARITY_THRESHOLD && !duplicate && Uri.parse(url).scheme === 'https') {
			duplicate = url;
		}
	});

	return duplicate;
}
