import { existsSync } from 'fs';
import { join } from 'path';
import { showStatusMessage, postError } from './common';
import { sendTelemetryData } from './telemetry';
// eslint-disable-next-line @typescript-eslint/no-var-requires
const extract = require('extract-zip');
export async function unzipTemplates(templateZip: string, docsAuthoringHomeDirectory: string) {
	try {
		showStatusMessage(`templateZip: ${templateZip}`);
		if (existsSync(templateZip)) {
			await extract(templateZip, { dir: docsAuthoringHomeDirectory });
			showStatusMessage(`Zip has been extracted to ${docsAuthoringHomeDirectory}`);
		}
	} catch (error) {
		const errorMessage = (error as Error).message;
		sendTelemetryData('unzipTemplates', errorMessage);
		postError(`Cannot unzip ${templateZip}.`);
		return showStatusMessage(`Cannot unzip ${templateZip}.`);
	}
	return true;
}
