'use strict';

import { commands, ExtensionContext, window, workspace } from 'vscode';
import { applyTemplateCommand } from './controllers/template-controller';
import { showStatusMessage } from './helper/common';
import { Reporter, sendTelemetryData } from './helper/telemetry';

export function activate(context: ExtensionContext) {
	context.subscriptions.push(new Reporter(context));
	// Creates an array of commands from each command file.
	const TemplateCommands: any = [];
	applyTemplateCommand().forEach(cmd => TemplateCommands.push(cmd));

	try {
		TemplateCommands.map((cmd: any) => {
			const commandName = cmd.command;
			const command = commands.registerCommand(commandName, () => cmd.callback(context));
			context.subscriptions.push(command);
		});
	} catch (error) {
		const errorMessage = `learn-article-templates failed to activate. Error registering commands with vscode extension context: ${error}`;
		showStatusMessage(errorMessage);
		sendTelemetryData('activate', errorMessage);
	}
	// if the user changes markdown.showToolbar in settings.json, display message telling them to reload.
	workspace.onDidChangeConfiguration((e: any) => {
		if (e.affectsConfiguration('docs.templates.githubID') ||
					e.affectsConfiguration('docs.templates.alias') ||
					e.affectsConfiguration('docs.templates.learn_repo_id') ||
					e.affectsConfiguration('docs.templates.learn_product') ||
					e.affectsConfiguration('docs.templates.learn_level') ||
					e.affectsConfiguration('docs.templates.learn_role')) {
			window
				.showInformationMessage(
					'Your updated configuration has been recorded, but you must reload to see its effects.',
					'Reload'
				)
				.then(res => {
					if (res === 'Reload') {
						commands.executeCommand('workbench.action.reloadWindow');
					}
				});
		}
	});
}

// this method is called when your extension is deactivated
export function deactivate() {
	showStatusMessage('Deactivating extension.');
}
