// To add a new template to be supported by the AI functionality, add a new object to availableTemplates.
// Each template contains:
// 1. A criteria string, which is a description of the type of article that the template is for.
//    This is used for Azure OpenAI to determine the article type
// 2. A markdown template, which is the template that is used to create the markdown file for the article.
//    The AI is instructed to fill in the template where indicated by \`\`\`placeholders\`\`\`.
// 3. A YAML template
//    The AI is instructed to create an outline for the article based on the structure of this YAML.

