// Max length for user input fields in characters (includes spaces)
const maxDescriptionLength = 800; // ~150 words, ~200 tokens
const maxContextLength = 5000; // ~1000 words, ~1500 tokens
const minDescriptionLength = 80; // Minimum character length required for the description field
const minzContextLength = 0;

export function getWebviewContent(): string {
	return `<!DOCTYPE html>
	<html lang="en">
	<head>
			<meta charset="UTF-8">
			<meta name="viewport" content="width=device-width, initial-scale=1.0">
			<title>AI-Generated Template</title>
			<link rel="stylesheet" href="https://static.docs.com/ui/latest/site/en-us/styles/site-ltr.css"/>
			<style>
					textarea, select {
							margin-top: 3px;
							position: relative;
					}
					textarea::placeholder {
						color: #666 !important;
						font-style: italic;
				}
					textarea.active-element:active,
					textarea.active-element:focus,
					select.active-element:active,
					select.active-element:focus {
							border-color: #75b6e7;
							outline: none;
							box-shadow: 0 0 0 .125rem #75b6e7;
					}
					p {
							margin-bottom: 3px!important;
							margin-top: 2px!important;
					}
					.content {
						padding: 3rem;
					}
					.character-counter-container {
							position: relative;
							margin-top: 24px!important;
					}
					.character-counter {
							position: absolute;
							bottom: 3px;
							right: 12px;
							font-size: 0.8rem;
							color: #666;
							cursor: pointer;
							background-color: #171717;
					}
					.character-counter-exceeded {
							color: #ff0000;
					}
					.submit-button {
							width: 5rem;
							margin-top: 3px;
					}
					ul {
							margin: 0;
							margin-left: 38px;
					}
			</style>
	</head>
	<body class="theme-dark">
	<div class="content">
			<div>
					<h1 class="font-size-h1">AI-Generated Template</h1>
					<p class="font-size-sm">Use this tool to generate a template for your article using artificial intelligence. The template will be based on the article description and optionally additional content you provide.</p>
			</div>
			<div class="character-counter-container">
					<label class="label font-size-h5" for="article-description">Article description</label>
					<p class="font-size-sm">Enter a 1-3 sentence description of the article you are creating. This will be used to generate a custom Markdown template, as well as starter content such as heading titles and introductory sentences. Be as clear and detailed as possible for best results.</p>
					<textarea class="textarea active-element" min-characters="${minDescriptionLength}" max-characters="${maxDescriptionLength}" id="article-description" placeholder="Example: This article will focus on discovering the essential techniques for..."></textarea>
					<span class="character-counter" id="article-description-counter">${maxDescriptionLength}</span>
			</div>
			<div class="character-counter-container">
					<label class="label font-size-h5" for="additional-context">(Recommended) Additional information</label>
					<p class="font-size-sm">Paste in any additional information or context that you would like to be included in the article. This is not required, but it is recommended for the best experience.</p>
					<textarea class="textarea active-element" min-characters="${minzContextLength}" max-characters="${maxContextLength}" id="additional-context" rows="8" placeholder="Notes, information from sources, etc."></textarea>
					<span class="character-counter" id="additional-context-counter">${maxContextLength}</span>
			</div>
			<button id="submit-button" class="button button-primary button-filled margin-top-xxs submit-button" onclick="submitArticleDescription()" disabled title="Please provide a more detailed description.">Submit</button>
			<script>
					const vscode = acquireVsCodeApi();
					window.addEventListener('message', event => {
							const message = event.data;
							const button = document.getElementById('submit-button');
							if (message.command === 'setLoading' && button) {
									button.classList.add('is-loading');
							}
							if (message.command === 'removeLoading' && button) {
									button.classList.remove('is-loading');
							}
					});
	
					// Handle character counting and limit
					function updateCharacterCount(textarea, counter, button) {
							const maxCharacterCount = textarea.getAttribute('max-characters');
							const minCharacterCount = textarea.getAttribute('min-characters');
							const currentCharacterCount = textarea.value.length;
							const remainingCharacterCount = maxCharacterCount - currentCharacterCount;
							counter.textContent = remainingCharacterCount;
							if (remainingCharacterCount < 0) {
									counter.classList.add('character-counter-exceeded');
									// Disable the submit button when the limit is reached
									button.disabled = true;
							} else {
									counter.classList.remove('character-counter-exceeded');
									// Enable the submit button when there are remaining characters
									button.disabled = false;
							}
							if (currentCharacterCount < minCharacterCount) {
								button.disabled = true;
							} else {
								button.disabled = false;
							}
						}
	
					const descriptionTextarea = document.getElementById('article-description');
					const descriptionCounter = document.getElementById('article-description-counter');
					const submitButton = document.getElementById('submit-button');
	
					// Initialize character count for description textarea
					updateCharacterCount(descriptionTextarea, descriptionCounter, submitButton);
	
					descriptionTextarea.addEventListener('input', () => {
							updateCharacterCount(descriptionTextarea, descriptionCounter, submitButton);
					});
	
					const contextTextarea = document.getElementById('additional-context');
					const contextCounter = document.getElementById('additional-context-counter');
	
					// Initialize character count for context textarea
					updateCharacterCount(contextTextarea, contextCounter, submitButton);
	
					contextTextarea.addEventListener('input', () => {
							updateCharacterCount(contextTextarea, contextCounter, submitButton);
					});
	
					function submitArticleDescription() {
							const description = descriptionTextarea.value.trim();
							const context = contextTextarea.value.trim();
							if (description) {
									vscode.postMessage({
											command: 'submit',
											text: description,
											context: context,
									});
							}
					}
			</script>
	</div>
	</body>
	</html>
	`;
}
