'use strict';

import { readFileSync } from 'fs';
import { extname } from 'path';
import { Position, TextDocument, Uri, window, workspace } from 'vscode';
import { generateTimestamp, showStatusMessage } from '../helper/common';
import { alias, gitHubID, missingValue } from '../helper/user-settings';

export function applyDocsTemplate(templatePath: string) {
	const templateExtension = extname(templatePath);
	const newFile = Uri.parse(`untitled:New-Topic.${templateExtension}`);
	workspace.openTextDocument(newFile).then((textDocument: TextDocument) => {
		window.showTextDocument(textDocument, 1, false).then(
			textEditor => {
				const content = readFileSync(templatePath, 'utf8');
				textEditor.edit(edit => {
					try {
						let updatedContent;
						const { msDateValue } = generateTimestamp();
						// replace metadata placeholder values with user settings and dynamic values then write template content to new file.
						if (!gitHubID && !alias) {
							updatedContent = content
								.replace(/\{@date\}/g, msDateValue)
								.replace(/\{github-id\}/g, missingValue)
								.replace(/\{ms-alias\}/g, missingValue);
							edit.insert(new Position(0, 0), updatedContent);
						} else if (!gitHubID) {
							updatedContent = content
								.replace(/\{@date\}/g, msDateValue)
								.replace(/\{github-id\}/g, missingValue)
								.replace(/\{ms-alias\}/g, alias);
							edit.insert(new Position(0, 0), updatedContent);
						} else if (!alias) {
							updatedContent = content
								.replace(/\{@date\}/g, msDateValue)
								.replace(/\{github-id\}/g, gitHubID)
								.replace(/\{ms-alias\}/g, missingValue);
							edit.insert(new Position(0, 0), updatedContent);
						} else {
							updatedContent = content
								.replace(/\{@date\}/g, msDateValue)
								.replace(/\{github-id\}/g, gitHubID)
								.replace(/\{ms-alias\}/g, alias);
							edit.insert(new Position(0, 0), updatedContent);
						}
					} catch (error) {
						showStatusMessage(error);
					}
				});
			},
			(error: any) => {
				showStatusMessage(error);
			}
		);
	});
}

export function applyAITemplate(generatedContent: string) {
	const newFile = Uri.parse('untitled:' + getTitle(generatedContent) + '.md');

	// Trim off the short_title field, as it was just needed to name the file
	generatedContent = generatedContent.replace(/^short_title:.*\n/gm, '');
	const { msDateValue } = generateTimestamp();

	workspace.openTextDocument(newFile).then((textDocument: TextDocument) => {
		window.showTextDocument(textDocument, 1, false).then(
			textEditor => {
				textEditor.edit(edit => {
					try {
						const metadataPairs = [
							{
								pattern: '#Required; your GitHub user alias, with correct capitalization',
								replacement: gitHubID
							},
							{ pattern: '#Required; microsoft alias of author', replacement: alias },
							{ pattern: '#Required; mm/dd/yyyy format', replacement: msDateValue }
						];

						let updatedContent = generatedContent;
						for (const { pattern, replacement } of metadataPairs) {
							if (replacement) {
								updatedContent = updatedContent.replace(new RegExp(pattern, 'g'), replacement);
							}
						}

						edit.insert(new Position(0, 0), updatedContent.trimStart());
					} catch (error) {
						showStatusMessage(error);
					}
				});
			},
			(error: any) => {
				showStatusMessage(error);
			}
		);
	});
}

// Returns title in kebab-case for the file name
function getTitle(generatedContent: string) {
	const shortTitleMatch = generatedContent.match(/short_title: (.*)/);
	let newFileTitle = 'New Article';

	if (shortTitleMatch) {
		newFileTitle = shortTitleMatch[1];
		newFileTitle = newFileTitle.replace(/[^a-zA-Z0-9\s]/g, ' ');
	}

	const words = newFileTitle.trim().split(/\s+/).slice(0, 5); // Only the first five words
	const title = words.map(word => word.toLowerCase()).join('-');
	return title;
}
