/* eslint-disable @typescript-eslint/no-var-requires */
'use strict';

import { showStatusMessage, postError } from '../helper/common';
import { files } from 'node-dir';
import { basename, dirname, extname, join } from 'path';
import { mkdirSync, readFileSync } from 'fs';
import { ExtensionContext, QuickPickItem, QuickPickOptions, window, workspace } from 'vscode';
import { applyDocsTemplate } from '../controllers/quick-pick-controller';
import { templateRepo } from '../helper/user-settings';
import { promptArticleDescription } from './ai-template-controller';
import { aoaiToken, docsToken } from '../ai-feature/api-settings';
import { sendTelemetryData } from '../helper/telemetry';
import { unzipTemplates } from '../helper/unzip-templates';
import { homedir } from 'os';
import * as tmp from 'tmp';

const { DownloaderHelper } = require('node-downloader-helper');

const telemetryCommand: string = 'templateSelected';
let commandOption: string;
export let moduleTitle;
const validTemplateExtensionFilter = ['.md', '.yml'];

export function applyTemplateCommand() {
	const commands = [{ command: applyTemplate.name, callback: context => applyTemplate(context) }];
	return commands;
}

export async function applyTemplate(context: ExtensionContext) {
	downloadTemplateZip(context);
}

export async function displayTemplates(templatesPath: string) {
	const displayTemplateList = 'Display template list';
	showStatusMessage(displayTemplateList);
	const templatesFolder = join(templatesPath, 'templates');
	files(templatesFolder, (err, files) => {
		if (err) {
			showStatusMessage(err);
			throw err;
		}

		// data structure used to store file name and path info for quick pick and template source.
		const quickPickMap = new Map();

		{
			files
				.filter(
					(file: any) => validTemplateExtensionFilter.indexOf(extname(file.toLowerCase())) !== -1
				)
				.forEach((file: any) => {
					if (basename(file).toLowerCase() !== 'readme.md') {
						quickPickMap.set(basename(file), join(dirname(file), basename(file)));
					}
				});
		}

		let data: any;
		try {
			const templateMappingJson = join(templatesPath, 'template-mapping', 'template-mapping.json');
			const templatesJson = readFileSync(templateMappingJson, 'utf8');
			data = JSON.parse(templatesJson);
		} catch (error) {
			postError(`${error.name} ${error.message}`);
			showStatusMessage(`${error.name} ${error.message}`);
			return;
		}

		// push quickMap keys to QuickPickItems
		const templates: QuickPickItem[] = [];
		let repo: string;
		if (workspace.workspaceFolders) {
			repo = workspace.workspaceFolders[0].name;
		}

		for (const key of quickPickMap.keys()) {
			try {
				data.templates.forEach((obj: any) => {
					const template = basename(obj.templateFileName);
					const friendlyName = obj.templateFriendlyName;
					const templatePath = quickPickMap.get(key);
					if (template === basename(templatePath)) {
						obj.repos.forEach((obj: any) => {
							if (obj === repo || obj === '*') {
								if (friendlyName) {
									templates.push({ label: friendlyName, description: key });
								} else {
									templates.push({ label: key });
								}
							}
						});
					}
				});
			} catch (error) {
				postError(`${error.name} ${error.message}`);
				showStatusMessage(`${error.name} ${error.message}`);
			}
		}

		templates.sort(function (a, b) {
			const firstLabel = a.label.toUpperCase();
			const secondLabel = b.label.toUpperCase();
			if (firstLabel < secondLabel) {
				return -1;
			}
			if (firstLabel > secondLabel) {
				return 1;
			}
			return 0;
		});

		// Create label and description for AI-generated template option, separate from other options
		// Only accessible if keys are present in settings
		const aiOptionLabel = 'AI-Generated Template';
		const aiOptionDescription =
			'Input an article description and let AI generate a template for you';
		if (docsToken && aoaiToken) {
			templates.push({ label: aiOptionLabel, description: aiOptionDescription });
		}

		const options: QuickPickOptions = {
			matchOnDescription: false
		};
		window.showQuickPick(templates, options).then(
			qpSelection => {
				if (!qpSelection || !qpSelection.label) {
					return;
				}
				// If user selects AI-generated template option, prompt user for description
				if (qpSelection.label === aiOptionLabel) {
					commandOption = aiOptionDescription;
					promptArticleDescription();
				} else {
					// Handle user selection of existing template options
					if (qpSelection.label) {
						let template: string;
						if (qpSelection.description) {
							template = qpSelection.description;
						} else {
							template = qpSelection.label;
						}
						const templatePath = quickPickMap.get(template);
						applyDocsTemplate(templatePath);
						commandOption = template;
						showStatusMessage(`Applying ${template} template.`);
					}
				}
				sendTelemetryData(telemetryCommand, commandOption);
			},
			(error: any) => {
				showStatusMessage(error);
			}
		);
	});
}

// download a copy of the template repo to the "Learn Authoring" directory.  no .git-related files will be generated by this process.
export async function downloadTemplateZip(_context: ExtensionContext) {
	try {
		const docsAuthoringHomeDirectory = join(homedir(), 'Docs Authoring');
		const { name, removeCallback } = tmp.dirSync({ unsafeCleanup: true });
		const localTemplateRepoPath = name;
		showStatusMessage(`Temp working directory ${localTemplateRepoPath} has been created.`);
		const downloadZip = new DownloaderHelper(templateRepo, localTemplateRepoPath);
		downloadZip.on('end', async () => {
			showStatusMessage(
				`Template repo zip file successfully downloaded to ${localTemplateRepoPath}.`
			);
			mkdirSync(localTemplateRepoPath, { recursive: true });
			await unzipTemplates(
				`${localTemplateRepoPath}/content-templates-main.zip`,
				docsAuthoringHomeDirectory
			);
			displayTemplates(`${docsAuthoringHomeDirectory}/content-templates-main`);
			await removeCallback();
		});
		downloadZip.on('error', async error => {
			showStatusMessage(`Error downloading templates from ${templateRepo}.`);
			postError(`Error downloading templates from ${templateRepo}.`);
			sendTelemetryData(
				'runDownloader',
				`Error downloading templates from ${templateRepo}. error: ${error}`
			);
		});
		downloadZip.start().catch(error => {
			showStatusMessage(`Error downloading templates from ${templateRepo}.`);
			postError(`Error downloading templates from ${templateRepo}.`);
			sendTelemetryData(
				'runDownloader',
				`Error downloading templates from ${templateRepo}. error: ${error}`
			);
		});
	} catch (error) {
		postError(error);
		showStatusMessage(error);
	}
}
