'use-strict';

import { workspace } from 'vscode';
const config = workspace.getConfiguration('docs.templates');

// settings.json values
export const gitHubID: string = config.get('githubid');
export const alias: string = config.get('alias');
export const missingValue: string = 'NO VALUE SET';
export const templateRepo: string = config.get('template_repo');
export const docsToken: string = config.get('docs_token');
export const aoaiToken: string = config.get('ai_token');
